.vacation {
  display: flex;
  flex-direction: row !important;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-radius: 10px;
  background: rgba(214, 185, 252, 0.5);
  padding: 5%;
  margin-top: 5%;
  position: relative;

  .left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;

    h5 {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin: 0%;
    }

    p {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }

    .extra {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
    }

    .extra p,
    .extra span {
      color: rgba(234, 234, 234, 0.8);
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
      margin: 0%;
    }

    .extra span:hover {
      text-decoration: underline;
      cursor: pointer;
    }

    .extra img {
      margin: 0%;
      padding-left: 5%;
    }

    .description {
      display: flex;
      flex-direction: column;
      align-items: flex-start !important;
    }

    .description p {
      margin-top: 5%;
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }

    .info {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 15%;
      margin-top: 3%;
    }
  }

  button {
    border: 4px solid #ffaa50;
    background: none;
    padding: 1%;
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 29px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  button:hover {
    background: rgba(255, 170, 80, 0.5);
    border: none;
  }
}

.inActive {
  display: none;
}

.popup {
  position: fixed;
  width: 30%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #eaeaea;
  border-radius: 10px;
  z-index: 10;
  margin-top: 3%;
  margin-bottom: 3%;
  overflow-y: scroll img {
    position: absolute;
    right: 5%;
    top: 4%;
  }

  h1 {
    background-color: #ffaa50;
    margin: 0%;
    padding: 5% 5% 10% 5%;
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 58px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2%;
    width: 100%;

    p {
      color: #101010;
      font-family: "Rubik";
      font-size: 20px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }
  }

  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .element {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      padding-left: 90px;
    }

    label {
      color: #101010;
      font-family: "Rubik";
      font-size: 20px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
      margin: 1%;
    }

    input,
    textarea {
      border-radius: 10px;
      border: 5px solid #ffaa50;
      width: 70%;
      height: 82px;
      color: rgba(16, 16, 16, 0.5);
      font-family: "Rubik";
      font-size: 20px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
      padding-left: 5%;
    }

    textarea {
      padding-top: 30px;
    }

    button {
      background: #ffaa50;
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      padding: 5%;
      margin-top: 5%;
      border: none;
    }

    button:hover {
      background: rgba(255, 131, 0, 1);
    }

    .bottom {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
      margin-top: 5%;
      margin-bottom: 5%;

      input {
        width: 29px;
        height: 30px;
        border-radius: 5px;
        border: 1px solid #101010;
      }
      input[type="checkbox"]:checked {
      }
    }
  }
}
@media (max-width: 1024px) {
  .vacation {
    .left {
      .description {
        div {
          p {
            font-size: 23px;
          }
        }
      }
      margin-right: 15px;
      h5 {
        font-size: 23px;
      }
      p {
        font-size: 23px;
      }
      .extra {
        span {
          font-size: 23px;
        }
      }
    }
    button {
      font-size: 23px;
    }
    .popup {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 50%;
      h1 {
        font-size: 23px;
        padding: 5% 5%;
      }
      img {
        width: 20px;
        top: 2.5%;
      }
      .form {
        padding: 0;
        p {
          font-size: 18px;
        }
      }
      form {
        .element {
          width: 90%;
          padding-left: 0 !important;
        }
        .bottom {
          width: 90%;
          label {
            margin-left: 10px;
            font-size: 18px;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .vacation {
    .left {
      .description {
        div {
          p {
            font-size: 21px;
          }
        }
      }
      p {
        font-size: 21px;
      }
      .extra {
        span {
          font-size: 21px;
        }
      }
    }
    button {
      font-size: 21px;
    }
    .popup {
      width: 70%;
      h1 {
        font-size: 18px;
        padding: 5% 5%;
      }
      img {
        width: 20px;
        top: 2.5%;
      }
      .form {
        padding: 0;
        p {
          width: 90%;
        }
        input,
        textarea {
          height: 35px;
          width: 80%;
          font-size: 12px;
          padding-left: 10px;
        }
        label {
          font-size: 15px;
          margin-top: 15px;
        }
        p {
          font-size: 15px;
        }
        button {
          padding: 2%;
          font-size: 20px;
        }
      }
      form {
        .bottom {
          input,
          textarea {
            width: 20px;
          }
          label {
            margin-left: 10px;
            font-size: 15px;
          }
        }
      }
    }
  }
}
@media (max-width: 450px) {
  .vacation {
    flex-direction: column !important;
    align-items: center;
    justify-content: center;
    .left {
      .description {
        div {
          p {
            font-size: 13px;
          }
        }
      }
      justify-content: center;
      align-items: center;
      margin: 0;
      width: 100%;
      h5 {
        font-size: 13px;
        text-align: center;
      }
      p {
        font-size: 11px;
      }
      .extra {
        justify-content: center;
        span {
          font-size: 11px;
        }
        img {
          width: 15px;
        }
      }
    }
    button {
      margin-top: 20px;
      font-size: 15px;
    }
    .popup {
      h1 {
        font-size: 11px;
      }
      .form {
        input,
        textarea {
          font-size: 11px;
        }
        label {
          font-size: 11px;
          margin-top: 15px;
        }
        p {
          font-size: 11px;
        }
        button {
          padding: 3%;
          font-size: 15px;
        }
      }
      form {
        .bottom {
          label {
            margin-left: 10px;
            margin-top: 0px;
            font-size: 10px;
          }
        }
      }
    }
  }
}
