* {
  ::selection {
    color: #101010;
    background-color: #d6b9fc;
  }

  body,
  * {
    scrollbar-face-color: rgba(255, 255, 255, 0.1);
    scrollbar-track-color: #101010;
  }

  body::-webkit-scrollbar,
  *::-webkit-scrollbar {
    width: 6px;
    height: 5px;
    border-radius: 0px;
  }

  body::-webkit-scrollbar-thumb,
  *::-webkit-scrollbar-thumb {
    background: rgba(214, 185, 252, 0.8);
    border-radius: 0px;
    -webkit-transition: background-color 0.2s;
    transition: background-color 0.2s;
  }

  body::-webkit-scrollbar-thumb:hover,
  *::-webkit-scrollbar-thumb:hover {
    background: rgba(214, 185, 252, 1);
  }

  body::-webkit-scrollbar-track,
  *::-webkit-scrollbar-track {
    background: #101010;
    border-radius: 0px;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: "Rubik";
  src: local("Rubik"), url("./Fonts/Rubik.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: "Rubik";
  src: local("Rubik-Regular"),
    url("./Fonts/Rubik-Regular.ttf") format("truetype");
  font-weight: 300;
}

@font-face {
  font-family: "Rubik";
  src: local("Rubik-SemiBold"),
    url("./Fonts/Rubik-SemiBold.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: "Rubik";
  src: local("Rubik-Bold"), url("./Fonts/Rubik-Bold.ttf") format("truetype");
  font-weight: 600;
}

* {
  a,
  button {
    cursor: pointer !important;
  }

  input:focus {
    outline: none !important;
  }
}
