.events {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 67%;
  margin: auto;

  h1 {
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 58px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .tabs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: auto;

    .inActive {
      color: #eaeaea;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      cursor: pointer;
      padding: 1% 2% 1% 2%;
    }

    .inActive:hover {
      background: rgba(214, 185, 252, 0.5);
      border-radius: 10px;
      padding: 1% 2% 1% 2%;
    }

    .active {
      color: #eaeaea;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      cursor: pointer;
      border-radius: 10px;
      background: rgba(255, 170, 80, 0.5);
      padding: 1% 2% 1% 2%;
    }

    .active2 {
      color: #eaeaea;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      cursor: pointer;
      border-radius: 10px;
      background: rgba(214, 185, 252, 0.5);
      padding: 1% 2% 1% 2%;
    }
  }

  .block {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10%;
  }

  .block a {
    text-decoration: none;
    color: #fff;
  }

  .block a:hover {
    text-decoration: underline;
  }

  img {
    margin-top: 5%;
  }

  .transport {
    position: absolute;
    right: 0;
    top: 30%;
  }
}
.line {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: -10;
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}
@media (max-width: 1024px) {
  .events {
    .schedule {
      width: 100%;
    }
    .transport {
      display: none;
    }
  }
}
@media (max-width: 768px) {
  .events {
    .line {
      width: 70%;
    }
    h1 {
      font-size: 40px;
    }
    .tabs {
      p {
        font-size: 37px !important;
      }
    }
  }
}
@media (max-width: 450px) {
  .events {
    width: 80%;
    h1 {
      font-size: 17px;
    }
    .tabs {
      p {
        font-size: 13px !important;
      }
    }
    .schedule {
      width: 130%;
    }
  }
}
